import React, { useContext, useState } from "react";
import { makeStyles, useTheme } from "@material-ui/core";
import { motion } from "framer-motion";
import LoaderContext from "../contexts/loaderContext";
import ThemeContext from '../contexts/themeContext';

const LoaderContainer = ({ children }) => {
    const { setIsLoading } = useContext(LoaderContext);
    const [visible, setVisible] = useState(true); // local visibility state
    const classes = useStyles();
    const theme = useTheme();
    const { isDarkMode } = useContext(ThemeContext);

    const circleRadius = 80;
    const circumference = 2 * Math.PI * circleRadius;

    const loadingDuration = 2.0;
    const opacityDelay = 5.0;
    const opacityFadeOutDuration = 2.0;

    return visible ? (
        <motion.div
            initial={{ opacity: 1 }}
            animate={{ opacity: 0 }}
            transition={{ delay: opacityDelay, duration: opacityFadeOutDuration, ease: "easeInOut" }}
            onAnimationComplete={() => {
                setIsLoading(false);
                console.log("Loader animation completed");
                setTimeout(setVisible(false), opacityFadeOutDuration * 1000); // hide the loader
            }}
            className={classes.container}
        >
            <div className={classes.circleWrapper}>
                <svg width="200" height="200" className={classes.loaderSvg}>
                    <circle
                        cx="50%"
                        cy="50%"
                        r={circleRadius}
                        fill="none"
                        stroke={isDarkMode ? theme.palette.action.disabledBackground : theme.palette.primary.main}
                        strokeWidth="6"
                    />
                    <motion.circle
                        cx="50%"
                        cy="50%"
                        r={circleRadius}
                        fill="none"
                        stroke={theme.logoColor.secondary}
                        strokeWidth="6"
                        strokeLinecap="round"
                        strokeDasharray={circumference}
                        strokeDashoffset={circumference}
                        animate={{ strokeDashoffset: 0 }}
                        transition={{ duration: loadingDuration, ease: "easeInOut" }}
                    />
                </svg>
                <div className={classes.centerContent}>
                    {children}
                </div>
            </div>
        </motion.div>
    ) : null;
};

const useStyles = makeStyles((theme) => ({
    container: {
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundColor: theme.palette.background.default,
        zIndex: 10000,
    },
    circleWrapper: {
        position: "relative",
        width: 200,
        height: 200,
    },
    loaderSvg: {
        position: "absolute",
        top: 0,
        left: 0,
        transform: "rotate(-90deg)",
    },
    centerContent: {
        position: "absolute",
        top: "50%",
        left: "50%",
        transform: "translate(-50%, -50%)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        textAlign: "center",
        pointerEvents: "none", // prevent accidental clicks while loading
    },
}));

export default LoaderContainer;
